.banner{
    height: 95vh;
    position: absolute;
    background-image: -webkit-linear-gradient(47deg, rgba(65, 88, 208, 0.8) 0%, rgba(200, 80, 192, 0.8) 46%, rgba(255, 204, 112, 0.8) 100%);
    background-image: -o-linear-gradient(47deg, rgba(65, 88, 208, 0.8) 0%, rgba(200, 80, 192, 0.8) 46%, rgba(255, 204, 112, 0.8) 100%);
    background-image: linear-gradient(43deg, rgba(65, 88, 208, 0.8) 0%, rgba(200, 80, 192, 0.8) 46%, rgba(255, 204, 112, 0.8) 100%);
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 10vh, 0 80%);
            clip-path: polygon(0 0, 100% 0, 100% 10vh, 0 80%);
    background-position: top;
    background-image: cover;
    padding: 0;
    margin: 0;
    width: 100%;
    overflow-x: hidden;
}

.header__text-box {
  position: relative;
  top: 20%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.heading-primary {
  text-transform: uppercase;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  margin-bottom: 6rem;
  color: white;
}

.heading-primary--main {
  display: block;
  font-size: 4rem;
  font-weight: 700;
  letter-spacing: 3.5rem;
  -webkit-animation-name: moveInTop;
          animation-name: moveInTop;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}



@-webkit-keyframes moveInTop {
  0% {
      opacity: 0;
      -webkit-transform: translateY(-3rem);
              transform: translateY(-3rem);

  }

  100% {
      opacity: 1;
      -webkit-transform: translateX(0);
              transform: translateX(0);
  }
}



@keyframes moveInTop {
  0% {
      opacity: 0;
      -webkit-transform: translateY(-3rem);
              transform: translateY(-3rem);

  }

  100% {
      opacity: 1;
      -webkit-transform: translateX(0);
              transform: translateX(0);
  }
}




@media (min-width: 767px) and (max-width: 1400px) {
  .banner {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 20vh, 0 80%);
            clip-path: polygon(0 0, 100% 0, 100% 20vh, 0 80%);
  }
  .heading-primary--main {
    font-size: 2rem;
    letter-spacing: 2.5rem;
  }
    
  /* CSS */
  
}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/





@media (min-width: 0px) and (max-width: 767px){
  .banner {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 20vh, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 20vh, 0 100%);
  }
  .heading-primary--main {
    font-size: 2rem;
    letter-spacing: 2.5rem;
  }
  
}


